<template>
  <div class="form-elements">
    <div class="row">
      <div class="col-md-12">
        <vuestic-widget :headerText="'menu.teams.create' | translate">
          <div class="row row-reverse">
            <div class="col-6">
              <div class="row row-reverse">
                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('name')}">
                    <div class="input-group">
                      <input
                        id="name"
                        name="name"
                        v-model="name"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('name')"></i>
                      <label class="control-label" for="name">{{'team.name'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('name')"
                             class="help text-danger">{{
                        errors.first('name')
                        }}
                      </small>
                    </div>
                  </div>
                </div>

                <div class="col">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('name_en')}">
                    <div class="input-group">
                      <input
                        id="name_en"
                        name="name_en"
                        v-model="name_en"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('name_en')"></i>
                      <label class="control-label" for="name_en">{{'team.name_en'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('name_en')"
                             class="help text-danger">{{
                        errors.first('name_en')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>
              <div class="row row-reverse">
                <div class="col-9">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('position')}">
                    <div class="input-group">
                      <input
                        id="position"
                        name="position"
                        v-model="position"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('position')"></i>
                      <label class="control-label" for="position">{{'team.position'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('position')"
                             class="help text-danger">{{
                        errors.first('position')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
                <div class="col-3">
                  <div class="form-group with-icon-right"
                       :class="{'has-error': errors.has('sort')}">
                    <div class="input-group">
                      <input
                        id="sort"
                        name="sort"
                        v-model="sort"
                        type="number"
                        v-validate="'required'"
                        required/>
                      <i
                        class="fa fa-exclamation-triangle icon-right input-icon"
                        v-show="errors.has('sort')"></i>
                      <label class="control-label" for="sort">{{'team.sort'
                        | translate}}</label><i class="bar"></i>
                      <small v-show="errors.has('sort')"
                             class="help text-danger">{{
                        errors.first('sort')
                        }}
                      </small>
                    </div>
                  </div>
                </div>
              </div>
            </div>

            <div class="col-6">
              <div class="row">
                <textarea
                  id="degrees"
                  name="degrees"
                  v-model="degrees"
                  class="col"
                  :palceholder="'team.degrees' | translate"
                  style="min-height: 200px;"
                ></textarea>
              </div>
            </div>
          </div>

          <div class="row">
            <div class="col mt-5 text-center">
              <button class="btn btn-success"  @click="add">
                {{'buttons.submit' | translate}}
              </button>
            </div>
          </div>          
        </vuestic-widget>        
      </div>
    </div>
  </div>
</template>

<script>
  import Vue from 'vue'
  import api from 'src/packages/Api'
  
  export default {
    name: 'create-team',
    data () {
      return {
        name: '',
        name_en: '',
        position: '',
        degrees: '',
        sort: 0
      }
    },
    methods: {
      add () {
        this.$validator.validateAll().then((result) => {
          if (result) {
            const data = {
              name: this.name,
              name_en: this.name_en,
              sort: this.sort,
              degrees: this.degrees,
              position: this.position              
            }

            api.post('teams', data).then(response => {
              this.$store.dispatch('addToastMessage',
                { text: Vue.i18n.translate('team.created'), type: 'success' }
              )
              this.$router.push({ name: 'team', params: { id: response.data.id } })
            }).catch(error => {
              this.$store.dispatch('addToastMessage',
                { text: Vue.i18n.translate('errors.any'), error }
              )
            })
          }
        })
      }
    }
  }
</script>

